    height: 100%;
    width: 100%;
    z-index: -1;
} */

.footer .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 3rem 0;
}

.logo-footer {
    width: 100px;
}

.logo-footer img {
    width: 100%;
}

.footer .top {
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
}

.footer .top .logo-footer {
    display: flex;
    align-items: center;
}

.footer .top .footer-icon {
    font-size: 3rem;
    margin-right: 1rem;
    color: white;
    cursor: pointer;
}


.footer .top .footer-icon2 {
    font-size: 3rem;
    margin-right: 1rem;
    color: white;
    cursor: pointer;
}

.footer h1 {
    font-size: 2rem;
    color: white;
}

.footer h2 {
    font-size: 2rem;
}

.footer h3 {
    color: white;
    font-size: 1rem;
}

.footer .col-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    padding: 1rem;
    width: 100%;
}

.col {
    width: 300px;
}

.footer h3 {
    margin: 1rem 0;
    text-transform: uppercase;
    color: rgb(226, 140, 10);
    font-size: .8rem;
}

.footer .license p {
    color: white;
}

.footer .license img {
    margin: 1rem 0;
}


.footer .license a:hover {
    color: rgb(8, 138, 245);
}

.footer p {
    font-family: 'Montserrat', sans-serif;
    margin: .5rem 0;
    color: white;
    font-weight: 500;
    text-align: left;
}


.copyright p {
    color: white;
    text-transform: uppercase;
}


.footer .top .footer-icon2 {
    display: none;
}


@media screen and (max-width: 940px) {
    .footer {
        height: 100%;
    }


    .footer h1, .footer h2, .footer h3 {
        text-align: left;
    }

    .license {
        width: 100%;
        text-align: center;
    }

.col h3, .col p {
    text-align: center;
}

    .license h3 {
        text-align: center;
    }

    .license p {
        text-align: center;
    }

    .footer .top .footer-icon {
        display: none;
}


.logo-footer {
    margin: auto;
}



}

